@font-face {
  font-family: "HurmeGeometricSans1_Light";
  src: url("../fonts/HurmeGeometricSans1 Light.otf");
}
@font-face {
  font-family: "MyriadRoman";
  src: url("../fonts/MyriadRoman.otf");
}
html {
  overflow-x: hidden;
}
body {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #000;
  overflow-x: hidden;
  @media (min-width: 992px) {
    font-size: 16px;
  }
}
a {
  text-decoration: none;
}