.btn-rounded {
  border-radius: 50px;
}
.btn-outline-white-red {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  border-color: $white;
  transition: all 0.3s;
  &:hover {
    color: $white;
    border-color: $red;
  }
  &:before {
    background: $red;
    border-radius: 50px;
  }
}
