.btn-redes {
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  transition-property: color;
  transition-duration: 0.3s;
  margin-left: 0.5rem;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  &:before {
    border-radius: 3px;
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: scaleY(0);
    transform-origin: 50% 100%;
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }
  
  &.fb:before {
    background-color: #3b5998;
  }
  &.ig:before {
    background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #bc1888 75%,
    #833ab4 100%
    ) !important;
  }
  &.fb:hover {
    color: #ffffff;
  }
  &.ig:hover {
    color: #ffffff;
  }
  &.fb:hover:before {
    transform: scaleY(1);
  }
  &.ig:hover:before {
    transform: scaleY(1);
  }
}

// ----------
// Hovers
// ----------

.hvr-underline-from-center {
  transform: perspective(1px) translateZ(0);
}
@media (min-width: 992px) {
  .hvr-underline-from-center:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    height: 1px;
    transition-property: left, right;
    transition-duration: 0.3s;
  }
}
.hvr-underline-from-center.active:before,
.hvr-underline-from-center:hover:before,
.hvr-underline-from-center:focus:before,
.hvr-underline-from-center:active:before {
  left: 0;
  right: 0;
}

.hvr-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
}
.hvr-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s;
}
.hvr-center-out:hover:before,
.hvr-center-out:focus:before,
.hvr-center-out:active:before {
  transform: scale(1.1);
}