.menu-toggle {
  height: 32px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer; }

.menu-toggle:hover {
  color: #999; }

.menu-toggle i,
.menu-toggle i:after,
.menu-toggle i:before {
  display: block;
  position: absolute;
  height: 3px;
  width: 35px;
  right: 0;
  background: #f32735;
  border-radius: 2px;
  transform: rotate(0deg);
  transition: .25s ease-in-out; }

.menu-toggle i:after,
.menu-toggle i:before {
  content: ''; }

.menu-toggle i:after {
  top: -10px; }

.menu-toggle i {
  top: 16px; }

.menu-toggle i:before {
  top: 10px; }

.menu-toggle.open i {
  transform: translateX(100px); }

.menu-toggle.open i:after {
  transform: translateX(-100px) rotate(45deg);
  top: 0px; }

.menu-toggle.open i:before {
  transform: translateX(-100px) rotate(-45deg);
  top: 0px; }

