footer {
  color: $white;
  background-color: $bg-black;
  .footer-1 {
    padding-top: 3rem;
    padding-bottom: 1rem;
    @media (min-width: 992px) {
      padding-top: 5rem;
      padding-bottom: 5rem;
    }
    h4 {
      margin-bottom: 1rem;
    }
    a {
      color: $white;
      &:hover {
        color: $red;
      }
    }
    li {
      display: inline-block;
      a {
        img {
          margin-right: 0.3rem;
          vertical-align: text-top;
          height: 16px;
        }
      }
    }
    .redes {
      font-size: 0.8rem;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .btn-redes {
        width: 30px;
      }
    }
  }
  hr {
    height: 1px;
    background-color: rgba($color: $white, $alpha: 0.8);
    margin: 0;
  }
  .footer-2 {
    padding-top: 0.5rem;
    padding-bottom: 1.5rem;
    font-size: 0.7rem;
  }
}