#colaboradores {
  background-color: $white;
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 2rem;
    @media (min-width: 992px) {
      font-size: 3rem;
      margin-bottom: 5rem;
    }
  }
  .row {
    div {
      text-align: center;
      a {
        transition: all 0.3s;
        &:hover {
          img {
            transform: scale(1.2);
            filter: saturate(1);
          }
        }
        img {
          filter: saturate(0);
          transition: all 0.3s;
        }
      }
    }
  }
}