.navbar {
  background-color: rgba($color: $bg-black, $alpha: 1);
  height: auto;
  transition: all 0.5s;
  @media (min-width: 992px) {
    // background-color: rgba($color: $bg-black, $alpha: 0.5);
    background-color: transparent;
    height: 100px;
  }
  .navbar-brand {
    font-family: "HurmeGeometricSans1_Light";
    display: flex;
    align-items: center;
    color: $white;
    transition: all 0.3s;
    &:hover {
      color: $red;
      opacity: 0.8;
    }
    .img-logo {
      background-image: url(../images/logo-nav.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      img {
        opacity: 0;
        height: 50px;
        transition: all 0.5s;
        @media (min-width: 992px) {
          height: 75px;
        }
      }
    }
  }
  .navbar-toggler {
    padding: 0;
    &:focus {
      box-shadow: none;
    }
    img {
      height: 40px;
      width: auto;
    }
  }
  ul {
    @media (min-width: 992px) {
      margin-left: 1rem;
      margin-right: 1rem;
    }
    .nav-item {
      font-family: "MyriadRoman";
      font-weight: bold;
      text-align: center;
      margin-top: 1vh;
      margin-bottom: 1vh;
      transition: all 0.15s;
      @media (min-width: 1200px) {
        margin-left: 1rem;
        margin-right: 1rem;
      }
      a {
        color: $white;
        &.active {
          color: $red;
        }
        &:before {
          background: $red;
        }
      }
      &:hover {
        a {
          color: $red;
        }
      }
    }
  }
  .btn {
    margin-top: 1vh;
    margin-bottom: 1vh;
    @media (min-width: 992px) {
      margin-left: 0.5rem;
      margin-right: 0.5rem;
    }
  }
}

.menu-toggle {
  height: 32px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}
.menu-toggle:hover {
  color: #999;
}
.menu-toggle i,
.menu-toggle i:after,
.menu-toggle i:before {
  display: block;
  position: absolute;
  height: 3px;
  width: 35px;
  right: 0;
  background: $red;
  border-radius: 2px;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}
.menu-toggle i:after,
.menu-toggle i:before {
  content: '';
}
.menu-toggle i:after {
  top: -10px;
}
.menu-toggle i {
  top: 16px;
}
.menu-toggle i:before {
  top: 10px;
}
.menu-toggle.open i {
  transform: translateX(100px);
}
.menu-toggle.open i:after {
  transform: translateX(-100px) rotate(45deg);
  top: 0px;
}
.menu-toggle.open i:before {
  transform: translateX(-100px) rotate(-45deg);
  top: 0px;
}